.title {
  margin-top: 5px;
  text-align: center;
  font-weight: 600;
  color: #0d253f;
}

.link {
  box-sizing: border-box;
  display: block;
  width: 170px;
  height: 100%;
  padding: 10px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.6), 0px 1px 1px rgba(0, 0, 0, 0.6),
    0px 2px 1px rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  text-decoration: none;
  transition: box-shadow 300ms linear;

  &:hover,
  &:focus {
    box-shadow: none;
    border: #0d253f 1px solid;
  }
}
